<template>
    <div class="details-container">
        <van-swipe class="my-swipe" :autoplay="3000" indicator-color="white">
            <van-swipe-item>
                <img :src="query.info.src" alt="">
            </van-swipe-item>
        </van-swipe>
        <div class="des">
            <div class="title">
                <div class="price">{{query.info.pri}}</div>
                <span>45</span>
            </div>
            <div class="van-hairline--bottom"></div>
            <p>
                鲜花名称： {{query.info.name}}
            </p>
            <div class="van-hairline--bottom"></div>
            <p>
                鲜花分类： {{"水果"}}
            </p>
            <div class="van-hairline--bottom"></div>
            <p>
                规格：{{"10"}}
            </p>
            <div class="van-hairline--bottom"></div>
            <p>
                产地：{{"中国"}}
            </p>
            <div class="van-hairline--bottom"></div>
            <div class="van-hairline--bottom"></div>
            <p>
                适用场景：{{"生活类"}}
            </p>
            <div class="van-hairline--bottom"></div>
        </div>
        <div class="detailed">
            <h2>水果详情</h2>
            <p>好吃的'水果,美味之选。</p>
            <div class="img">
                <img :src="banner1" alt="水果">
            </div>
            <div class="van-hairline--bottom"></div>
            <div class="comment">
                <h3>评论</h3>
                <div class="van-hairline--bottom"></div>
                <div class="des">
                    <p>小张： 这个新鲜吗？</p>
                </div>
            </div>
        </div>
        <div class="normal">
            <van-skeleton title avatar :row="3" :loading="loading">
                <div>没有更多了</div>
            </van-skeleton>
        </div>
        <van-tabbar style="flex-direction: row-reverse; padding: 10px;">
            <van-button type="danger" @click="carClick">加入购物车</van-button>
            <van-button type="primary" @click="clickD">立即收藏</van-button>
        </van-tabbar>
    </div>
</template>

<script>
import banner1 from "@/assets/banner/banner-2.jpeg"
import { Toast } from 'vant';

export default {
        data() {
            return {
                banner1,
                loading: true,
                query: {
                    info: {
                        src: "",
                        pri: "222",
                        name: "xxx"
                    }
                },
            };
    },
    methods: {
        clickD() {
            Toast('收藏成功');
            this.$store.commit("collection/addCar" , this.$route.query.info)
        },
        carClick() {
            Toast('加入成功');
            this.$store.commit("shoppingCar/addCar" , this.$route.query.info)
        }
    },
    mounted() {
        this.query = this.$route.query
        this.loading = false;
    },
}
</script>

<style lang="scss" scoped>
    .my-swipe .van-swipe-item {
        color: #fff;
        font-size: 20px;
        line-height: 150px;
        text-align: center;
        background-color: #39a9ed;
        img{
            width: 100%;
            height: 100%;
            object-fit: cover;
            display: block;
        }
    }
    .des{
        .title {
            display: flex;
            align-items: center;
            padding: 5px 10px;
            justify-content: space-between;
            .price{
                color: red;
                font-weight: bold;
                font-size: 26px;
            }
        }
        &>p{
            font-size: 16px;
            padding-left: 10px;
        }
    }
    .detailed{
        padding-left: 10px;
        p{
            font-size: 16px;
        }
        .img{
            margin-left: -10px;
            img{
                width: 375px;
            }
        }
    }
    .comment {
        .des {
            font-size: 16px;
        }
    }
    .normal{
        padding-bottom: 70px;
        text-align: center;
    }
</style>